export default {
	data() {
		return {
			tabs: [{
					tit: '待审核',
				},
				{
					tit: '已通过'
				},
				{
					tit: '驳回'
				}
			],
			current: 0,
			col: {
					col: '#FA9C22',
					theme: '待审核'
				}
		}
	},
	methods: {
		change(i) {
			this.current = i
			const col = [{
					col: '#FA9C22',
					theme: '待审核'
				},
				{
					col: '#1AB560',
					theme: '已通过'
				},
				{
					col: '#E4393D',
					theme: '驳回'
				}
			];
			this.col = col[i]

		}
	},
}
